@use '../module' as m;

.project-container {

    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0px 0px 60px 0px ;
    
    .project-img {
    background-color: white;
    width: 540px;
    margin: auto 20px;
    border-radius: 10px;

    }

    .img1 {
        box-shadow: 0 0 1em 0 #00ace198, 0 0 1em 0 #00ace198 
    }

    .img2 {
        box-shadow: 0 0 1em 0 #f05b5b93, 0 0 1em 0 #f05b5b93
    }

    .img3 {
        box-shadow: 0 0 1em 0 #f1c3888a, 0 0 1em 0 #f1c3888a
    }

    

    .project-description {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin: auto 70px;

        h2 {

            font-size: 2em;

        }
        
        .project-text {

            @include m.text-style;
            width: 418px;
            overflow: hidden;
            text-align: center; 
            margin: 5px 0px;
            font-size: 1.4em;
        }

    
        .btn-row {
            display: flex;

    
            .btn {
                @include m.small-button;
                text-decoration: none;
            }

            .btn ::before {
                @include m.small-button-before;
            }

            .btn::after {
                @include m.small-button-after;
            }
            
            @include m.glow-effect-class(btn);

            
        }

        .btn-row :active {
                width: 90px;
                height: 20px;
                margin: auto 15px
            }
    }
    
    .reverse {
        margin-right: 4em ;
    
        
        .project-text {
            margin: 20px 0px;
        }

    }

    
}

    
